@use "../base/variables" as v;
@use "../base/mixins" as m;

.testimoniales {
    h2 {
        @include m.headingBorder(v.$secundario, v.$primario);
    }

    @include m.tablet {
        @include m.grid(5, 4rem);
        align-items: center;
        .imagen {
            grid-column: 1 / 3;
        }
        .contenido {
            grid-column: 3 / 6;
        }
    }
}
.contenido {
    .testimonial {
        background-color: v.$primario;
        border: 0.5rem solid v.$negro;
        margin-bottom: 4rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        padding: 2rem;
        border-radius: 1rem;
        position: relative;

        p {
            margin: 0;
            padding-left: 6rem;
        }

        &::before {
            content: "";
            display: block;
            width: 5rem;
            height: 5rem;
            background-image: url(../../../build/img/comilla.svg);
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
        }

        @include m.tablet {
            max-width: 70%;

            &:first-of-type {
                margin-left: 10%;
            }
            &:nth-child(3) {
                margin-left: 20%;
            }
            &:nth-child(4) {
                margin-left: 30%;
            }
        }
    }
}
